/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Ubuntu", sans-serif;
  font-size: 1.6rem;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Overpass", sans-serif;
  color: hsl(208deg, 49%, 24%);
}

p {
  font-family: "Ubuntu", sans-serif;
  color: hsl(207deg, 13%, 34%);
}

a {
  text-decoration: none;
}

button {
  background-color: transparent;
  line-height: 1;
  padding: 0;
  border: solid 0.1rem black;
  border: none;
  cursor: pointer;
}

.header {
  background: rgb(255, 143, 112);
  background: linear-gradient(90deg, rgb(255, 143, 112) 0%, rgb(255, 61, 83) 100%);
  border-radius: 0 0 0 7.5rem;
  max-width: 144rem;
  position: relative;
  overflow: hidden;
}
.header::before {
  content: "";
  position: absolute;
  background-image: url("../img/bg-pattern-intro-mobile.svg");
  background-repeat: no-repeat;
  top: -30rem;
  left: -30rem;
  right: 0;
  bottom: 0;
}
.header__container {
  position: relative;
  padding: 5.5rem 2.5rem 15.5rem;
}
.header__navegation {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  height: 3rem;
  width: 8.5rem;
  object-fit: contain;
}
.header__button {
  border: none;
}
@media (min-width: 768px) {
  .header__button {
    display: none;
  }
}
.header__icon {
  width: 3.5rem;
  height: 3rem;
  object-fit: contain;
}
.header__content {
  padding: 5rem 1.5rem 0;
}
.header__heading {
  color: hsl(0deg, 0%, 100%);
  text-align: center;
  margin: 0;
}
.header__text {
  text-align: center;
  color: hsl(0deg, 0%, 100%);
  line-height: 1.5;
  margin: 2.5rem 0;
}
.header__information {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
}
.header__link {
  padding: 1.5rem 2rem;
  border-radius: 10rem;
  border: solid 0.1rem hsl(0deg, 0%, 100%);
  font-weight: 700;
  margin-top: 2.5rem;
}
.header__link:first-of-type {
  color: hsl(356deg, 100%, 66%);
  background-color: hsl(0deg, 0%, 100%);
}
.header__link:last-of-type {
  color: hsl(0deg, 0%, 100%);
  background-color: transparent;
}

.menu {
  width: 100%;
  margin-top: 5rem;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 1rem;
  height: 0;
  pointer-events: none;
  overflow: hidden;
  transition: all 500ms ease;
}
@media (min-width: 768px) {
  .menu {
    width: auto;
  }
}
.menu__boxes {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.menu__box, .menu__links {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.menu__button {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0 1rem 2rem;
  color: hsl(208deg, 49%, 24%);
  font-weight: 500;
}
.menu__arrow {
  transition: transform 350ms ease;
}
.menu__links {
  flex-direction: column;
  gap: 2.5rem;
  width: 100%;
  border-radius: 0.5rem;
  height: 0;
  overflow: hidden;
  pointer-events: none;
  transition: all 350ms ease;
  background-color: #efeff0;
}
.menu__link {
  color: hsl(207deg, 13%, 34%);
  font-weight: 500;
}
.menu__subscribe {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  margin-top: 2.5rem;
  padding: 1.5rem 0 0;
  border-top: solid 0.1rem hsl(240deg, 2%, 79%);
}
.menu__register:first-of-type {
  padding: 1.5rem 0;
}
.menu__register:last-of-type {
  color: hsl(0deg, 0%, 100%);
  background: rgb(255, 143, 112);
  background: linear-gradient(90deg, rgb(255, 143, 112) 0%, rgb(255, 61, 83) 100%);
  padding: 1.5rem 4rem;
  border-radius: 10rem;
}

.active-main-menu {
  padding: 3rem 2.5rem;
  height: auto;
  pointer-events: auto;
}

.active-submenu {
  height: auto;
  padding: 2.5rem 0;
  pointer-events: auto;
}

.up-arrow {
  transform: rotate(180deg);
}

.designed {
  padding: 5rem 2.5rem;
}
.designed__heading, .designed__title, .designed__text {
  text-align: center;
}
.designed__heading {
  margin: 5rem 0;
}
.designed__grid {
  display: grid;
  grid-template-rows: 32.5rem 1fr;
}
.designed__content {
  grid-row: 2/3;
  padding: 0 1rem;
}
.designed__title {
  font-size: 2.4rem;
  margin: 5rem 0 2.5rem;
  line-height: 1.25;
}
.designed__text {
  line-height: 1.5;
  margin: 0;
}
.designed__image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}